<template>
  <div>
    <button class="btn" @click="goRegister">注册页面</button>
    <button class="btn" @click="goHospitalInfo">医院介绍</button>
    <button class="btn" @click="goExpertInfo">专家介绍</button>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    goRegister: function () {
      this.$router.push({
        path: `/register`
      })
    },
    goHospitalInfo: function () {
      this.$router.push({
        path: `/hospitalInfo`
      })
    },
    goExpertInfo: function () {
      this.$router.push({
        path: `/expertIntroduction`
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/base"

.btn
  background-color : $color-theme-blue
  border : none
  height 44px
  width : 100px
  margin-top : 20px
  color : white

</style>



